export default {
	data() {
		return {
			list1: [{id:1},{id:2},{id:3},{id:4}],
			list2: [{id:1},{id:2},{id:3},{id:4},{id:5},{id:6},{id:7},{id:8},{id:9}],
			searchValue: '',
			isShowTitle: true
		}
	},
	methods: {
		onChange(v) {
			this.searchValue = v.detail
			if(this.isShowTitle != '') {
				this.isShowTitle = false
			}else{
				this.isShowTitle = true
			}
		}
	}
}
